<template>
  <el-main>
    <el-col
      :span="12" 
      :offset="6" 
      :xs="{ span: 19, offset: 3 }"
    >
      <div class="class-info">
        <el-alert
          type="info"
          title=""
          description="本表個人資料將作建檔處理，並於日後會務活動運作之目的內，由教會及同工作為聯絡通訊、關懷及相關合理運用。我們會盡善良管理人責任，妥善保管資料，避免外洩或不當用途之使用。"
          :closable="false"
        >
        </el-alert>
        <ul class="class-detail">
          <li>
            <div class="class-detail-title">報名條件：</div>
            <div class="class-detail-text">{{ subInfo.condition }}</div>
          </li>
          <li>
            <div class="class-detail-title">上課日期：</div>
            <div class="class-detail-text">{{ subInfo.date }}</div>
          </li>
          <li>
            <div class="class-detail-title">地點：</div>
            <div class="class-detail-text">{{ subInfo.location }}</div>
          </li>
          <li>
            <div class="class-detail-title">報名日期：</div>
            <div class="class-detail-text">{{ subInfo.endDate }}</div>
          </li>
        </ul>
      </div>

    
      <el-form ref="form" :model="form" :rules="rules" :label-position="labelPosition" label-width="105px" @submit.prevent.native="checkEnter">
        <el-form-item label="組別" prop="gcroup">
          <el-select
            v-model="form.gcroup"
            @change="onGcroupChange"
          >
            <el-option v-for="group in groupData" :value="group.group" :key="group.group">{{ group.group }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="小組" prop="group">
          <el-select v-model="form.group" >
            <el-option v-for="group in groupList"  :value="group" :key="group">{{ group }}</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model.trim="form.name"></el-input>
        </el-form-item>
        <el-form-item label="手機" prop="phone">
          <el-input v-model="form.phone" type="number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="cacheInfo">
          <el-checkbox-group v-model="form.cacheInfo">
            <el-checkbox label="記住我的資訊" name="cacheInfo"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" @click="checkMember('form')" :loading="sending">報名</el-button>
          <el-button size="small" @click="resetForm('form')" :disabled="sending">重置</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-main>
</template>

<script>
import { find, get } from 'lodash';
import axios from 'axios';
import { addSubSignApi, chkMemberApi } from '../api.js';

export default {
  data() {
    return {
      labelPosition: 'right',
      form: {
        gcroup: '',
        group: '',
        name: '',
        phone: '',
        cacheInfo: false,
        mid: '',
        dataChangeMsg: [],
      },
      rules: {
        gcroup: [
          { required: true, message: '請選擇組別', trigger: 'change' },
        ],
        group: [
          { required: true, message: '請選擇小組', trigger: 'change' },
        ],
        name: [
          { required: true, message: '請輸入名字', trigger: 'blur' },
        ],
        phone: [
          { required: true, message: '請輸入手機', trigger: 'blur' },
          { 
            validator: (rule, value, callback) => {
              const re = /^[09]{2}[0-9]{8}$/;
              if (!re.test(value)) {
                callback(new Error('請輸入正確手機格式'));
              } else {
                callback();
              }
            },
            trigger: 'blur'
          }
        ],
      },
      sending: false,
    }
  },
  props: {
    subInfo: {
      type: Object,
      required: true,
    },
    groupData: {
      type: Array,
      required: true,
    }
  },
  computed: {
    groupList: function() {
      const group = find(this.groupData, { group: this.form.gcroup });
      return get(group, 'list', []);
    }
  },
  methods: {
    checkMember(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sending = true;
          const h = this.$createElement;
          const self = this;

          axios
            .post(chkMemberApi, {
              group: this.form.group,
              Ename: this.form.name,
              Phone: this.form.phone,
            }).then((response) => {
              self.sending = false;

              const {
                IsChgShow,
                ApiTitle,
                DataChangeMsg,
                MID,
              } = response.data;
              
              if (IsChgShow) {
                const msgArr = [];
                DataChangeMsg.map((msg) => {
                  msgArr.push(h('li', null, msg));
                  return msg;
                });

                self.$msgbox({
                  title: ApiTitle,
                  message: h('ul', null, msgArr),
                  confirmButtonText: '我要更新資料＋報名',
                  showCancelButton: true,
                  cancelButtonText: '重新填寫',
                  beforeClose: (action, instance, done) => {
                    if (action === 'confirm') {
                      self.form.mid = MID;
                      self.form.dataChangeMsg = DataChangeMsg;
                      self.submitForm();
                      done();
                    } else {
                      self.resetForm('form');
                      done();
                    }
                  }
                })
              } else {
                self.form.mid = MID;
                self.form.dataChangeMsg = DataChangeMsg;
                self.submitForm();
              }
              
            }).catch((e) => {
              self.sending = false;
              self.$message({ message: '發送發生錯誤，請稍後再試', type: 'error' });
            });
        } else {
          return false
        }
      })
    },
    submitForm() {
      const self = this;
      this.sending = true;

      axios
        .post(addSubSignApi, {
          DataChangeMsg: this.form.dataChangeMsg,
          MID: this.form.mid,
          SID: this.subInfo.sid,
          CategoryID: this.subInfo.categoryID,
          gcroup: this.form.gcroup,
          group: this.form.group,
          Ename: this.form.name,
          Phone: this.form.phone,
          Gmail: '',
          Church: '',
        }).then((response) => {
          self.sending = false;

          const {
            IsApiError,
            ApiMsg,
          } = response.data;

          if (IsApiError) {
            self.$message({ message: ApiMsg, type: 'error' });
          }else {
            if (self.form.cacheInfo) {
              let date = new Date;
              date.setDate(date.getDate() + 30);

              self.$cookie.set('clc-sign-gcroup', self.form.gcroup, { expires: date });
              self.$cookie.set('clc-sign-group', self.form.group, { expires: date });
              self.$cookie.set('clc-sign-name', self.form.name, { expires: date });
              self.$cookie.set('clc-sign-phone', self.form.phone, { expires: date });
            } else {
              self.$cookie.delete('clc-sign-gcroup');
              self.$cookie.delete('clc-sign-group');
              self.$cookie.delete('clc-sign-name');
              self.$cookie.delete('clc-sign-phone');
            }

            self.resetForm('form');
            self.$emit('onSignSuccess', response.data);
          }
        }).catch((e) => {
          self.sending = false;
          self.$message({ message: '發送發生錯誤，請稍後再試', type: 'error' });
        });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    onGcroupChange: function() {
      this.form.group = '';
    },
    onResize() {
      this.labelPosition = window.innerWidth <= 768 ? 'top' : 'right';
    },
  },
  mounted() {
    window.addEventListener('resize', this.onResize);
    this.onResize();
    this.form.gcroup = this.$cookie.get('clc-sign-gcroup') || '';
    this.form.group = this.$cookie.get('clc-sign-group') || '';
    this.form.name = this.$cookie.get('clc-sign-name') || '';
    this.form.phone = this.$cookie.get('clc-sign-phone') || '';
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.onResize);
  }
}
</script>

<style scope>
.el-select {
  display: block !important;
}
</style>


